export default (src) => {
    return typeof src === 'string' ? loadImg(src) : loadElement(src);
};
const loadImg = (src) => new Promise((resolve, reject) => {
    const img = new Image;
    img.src = src;
    img.onload = () => resolve();
    img.onerror = reject;
});
const loadElement = (element) => Promise.all([element, Array.from(element.querySelectorAll('*'))]
    .reduce((paths, element) => {
    if (element instanceof Image) {
        paths.push(loadImg(element.getAttribute('src')));
    }
    return paths;
}, []));
